.flag {
    position: relative;
}


.flag img{
    width: 100%;
    display: block;
    border: 2px solid black;
    border-radius: 6px;
}

/* flipping action */ 
.flag .front{
    transform: rotateY(90deg);
    position: absolute;
}

.flipped .front{
    transform: rotateY(0deg);
}



