@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
  
/* base styles */
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: #13c773;
  color: #fff;
}

.App {
  max-width: 860px;
  margin: 40px auto;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #c23866;
  color: #fff;
}

.flag-grid {
  margin-top: 80px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px
}


.flag {
    position: relative;
}


.flag img{
    width: 100%;
    display: block;
    border: 2px solid black;
    border-radius: 6px;
}

/* flipping action */ 
.flag .front{
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    position: absolute;
}

.flipped .front{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}




